import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import connect from '@vkontakte/vkui-connect';
import PropTypes from 'prop-types';
import { Cell, Panel, Group, List, ListItem, Avatar, Div} from '@vkontakte/vkui';
import Icon24About from '@vkontakte/icons/dist/24/about';
import Icon24Services from '@vkontakte/icons/dist/24/services'



const User = props => (
  <Div>
    <Group title=" ">
    {props.fetchedUser &&
  			<ListItem
  				before={<Avatar src={props.fetchedUser.photo_200}/>}
  				description={props.fetchedUser.city.title}>
  				{`${props.fetchedUser.first_name} ${props.fetchedUser.last_name}`}
  			</ListItem>}
  		</Group>
      <Group>
        <List>
          <Cell before={<Icon24About />}> </Cell>
          <Cell before={<Icon24Services />}> </Cell>
          <Cell before={<Icon24About />}>    </Cell>
          <Cell before={<Icon24Services />}>  </Cell>
          <Cell before={<Icon24Services />}>     </Cell>
        </List>
      </Group>
  </Div>

);

User.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};


export default User;


