import React from 'react';
import PropTypes from 'prop-types';
import {platform,  IOS, ANDROID, Panel, PanelHeader, FixedLayout, Tabs, TabsItem, HorizontalScroll, Div, Button} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Icon24Add from '@vkontakte/icons/dist/24/add';

import Entry from './Entry';
import User from './User';

const osname = platform();

class Home extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeTab5: 'groups',
    };
  }

  render() {

    return(
        <Panel id="home" theme="white">
          <PanelHeader  >MFC Service ({osname === IOS ? 'IOS': 'Android'})</PanelHeader>
            <FixedLayout vertical="top">
              <Div style={{ margin:0, padding: 0}}>
                  {this.state.activeTab5 ===  'groups' ?  <Entry />: <User fetchedUser={this.props.fetchedUser}/>}
              </Div>
            </FixedLayout>
            <FixedLayout vertical="bottom">
                <Tabs >
                    <TabsItem
                      onClick={() => this.setState({ activeTab5: 'groups' })}
                      selected={this.state.activeTab5 === 'groups'}
                    >. </TabsItem>
                    <TabsItem
                      onClick={() => this.setState({ activeTab5: 'user' })}
                      selected={this.state.activeTab5 === 'user'}
                    > </TabsItem>
                </Tabs>
              </FixedLayout>
        </Panel>






    );
  }
}


export default Home;


